'use strict'

function getInfo(cells) {
  var places = [
    { index: 0, bases: [ 1, 3, 4 ] },
    { index: 1, bases: [ 3 ] },
    { index: 2, bases: [ 2, 3 ] },
    { index: 3, bases: [ 1 ] },
    { index: 6, bases: [ 1 ] }
  ]

  var output = {
    isFinished: false,
    nextMoves: []
  }

  function isChosen(cell) {
    return cells.includes(cell)
  }

  places.forEach(place => {
    var index = place.index

    place.bases.forEach(function (base) {
      if (output.isFinished) return

      var cell1 = index
      var cell2 = index + base
      var cell3 = index + base * 2

      if (isChosen(cell1) && isChosen(cell2) && isChosen(cell3)) {
        output.isFinished = true

        return
      }

      if (isChosen(cell2) && isChosen(cell3))
        return output.nextMoves.push(cell1)

      if (isChosen(cell1) && isChosen(cell3))
        return output.nextMoves.push(cell2)

      if (isChosen(cell1) && isChosen(cell2))
        return output.nextMoves.push(cell3)
    })
  })

  return output
}

function getCellsX(state) {
  return state.map(function (value, index) {
    return value === 'x'
      ? index
      : null
  })
}

function getCellsO(state) {
  return state.map(function (value, index) {
    return value === 'o'
      ? index
      : null
  })
}

function isDraw(state) {
  return state.every(function (value) {
    return value
  })
}

function getEmptyCells(state) {
  return state.map(function (value, index) {
    return value || index
  }).filter(Number.isInteger)
}

function randomvalue(array) {
  var randomIndex = Math.floor(Math.random() * array.length)

  return array[randomIndex]
}

function isFinished(state) {
  var cellsX = getInfo(getCellsX(state))
  var cellsO = getInfo(getCellsO(state))

  if (isDraw(state)) return true
  if (cellsX.isFinished || cellsO.isFinished) return true

  return false
}

function willWin(player, index, state) {
  state[index] = player

  return isFinished(state)
}

function getNextState(player, state) {
  if(isDraw(state)) return state

  var cellsX = getCellsX(state)
  var cellsO = getCellsO(state)

  var nextMoveX = getInfo(cellsX).nextMoves.find(function (cell) {
    return state[cell] === null
  })
  var nextMoveO = getInfo(cellsO).nextMoves.find(function (cell) {
    return state[cell] === null
  })

  var index

  switch (player) {
    case 'x':
      index = willWin('x', nextMoveX, state) && nextMoveX !== undefined
        ? nextMoveX
        : (nextMoveO !== undefined
          ? nextMoveO
          : nextMoveX)
      break
    case 'o':
      index = willWin('o', nextMoveO, state) && nextMoveO !== undefined
        ? nextMoveO
        : (nextMoveX !== undefined
          ? nextMoveX
          : nextMoveO)
      break
  }

  if (!index) {
    state[randomvalue(getEmptyCells(state))] = player

    return state
  }

  state[index] = player

  return state
}

function whoWon(state) {
  var cellsX = getInfo(getCellsX(state))
  var cellsO = getInfo(getCellsO(state))

  if (isDraw(state)) return null

  if (cellsX.isFinished) return 'x'
  if (cellsO.isFinished) return 'o'

  return false
}

exports.isFinished = isFinished

exports.getNextState = getNextState

exports.whoWon = whoWon